<template>
  <div class="main">
    <h1>VueRetroCursorText</h1>
    <h3>A Vue component</h3>
    (Refresh page to re-start animations!)
    <h4>Demo:</h4>
    <div class="demobox">
      <RetroCursorText
        v-for="(line, index) in text"
        :key="index"
        :text="line"
        :delay="1500 + index * 1500"
        :size="25"
        :hideCursorEnd="index < text.length - 1"
        :hideCursorStart="index > 0"
      />
    </div>
    <h4>Usage:</h4>
    Import and register component where you are going to use it. 
    After that you can use it in your template like this:
    <div class="demobox">
      <span style="color: grey">{{ this.start }}</span><span style="color: aqua"><strong>RetroCursorText</strong></span><span style="color: aqua"> text</span>="Hello world..." <span style="color: grey">{{ this.end }}</span>
    </div>
    <h4>Props:</h4>
    Besides the <strong>text</strong> prop, you can define the following
    properties to modify the component's behaviour:
    <h5>TYPE</h5>
    The <strong>type</strong> property can take two values, either "normal", or "wide".
    If none is defined, the default value is "normal".
    <br><br>Examples:
    <br><br>Normal:
    <div class="demobox">
      <span style="color: grey">{{ this.start }}</span><span style="color: aqua"><strong>RetroCursorText</strong></span><span style="color: aqua"> text</span>="Hello world..." <span style="color: aqua"> type</span>="normal" <span style="color: grey">{{ this.end }}</span>
    </div>
    <div class="demobox">
      <RetroCursorText text="Hello world..." type="normal" />
    </div>
    <br>Wide:
    <div class="demobox">
      <span style="color: grey">{{ this.start }}</span><span style="color: aqua"><strong>RetroCursorText</strong></span><span style="color: aqua"> text</span>="Hello world..." <span style="color: aqua"> type</span>="wide" <span style="color: grey">{{ this.end }}</span>
    </div>
    <div class="demobox">
      <RetroCursorText text="Hello world..." type="wide" />
    </div>

    <h5>SIZE</h5>
    The <strong>size</strong> property can modify the size of the text in 
    pixels - the default value is 20.
    <div class="demobox">
      <span style="color: grey">{{ this.start }}</span><span style="color: aqua"><strong>RetroCursorText</strong></span><span style="color: aqua"> text</span>="Hello world..." <span style="color: aqua"> :size</span>="30" <span style="color: grey">{{ this.end }}</span>
    </div>
    <div class="demobox">
      <RetroCursorText text="Hello world..." :size="30" />
    </div>

    <h5>DELAY</h5>
    The <strong>delay</strong> property can set a delay in milliseconds before the
    animation starts. This is what is used in the demo above to achieve
    the effect of the lines being displayed after one another.

    <h5>COLOR</h5>
    The <strong>color</strong> property can set the text color different
    from the default green.
    <div class="demobox">
      <span style="color: grey">{{ this.start }}</span><span style="color: aqua"><strong>RetroCursorText</strong></span><span style="color: aqua"> text</span>="Hello world..." <span style="color: aqua"> color</span>="red" <span style="color: grey">{{ this.end }}</span>
    </div>
    <div class="demobox">
      <RetroCursorText text="Hello world..." color="red" />
    </div>

    <h5>HIDECURSORSTART and HIDECURSOREND</h5>
    The <strong>hideCursorStart</strong> and <strong>hideCursorEnd</strong>
    properties can disable the blinking cursor at the beginning and the 
    end of the animation, respectively. This is also used in the 
    demo above to hide the cursor after each line but the last, and 
    to hide it before each line but the first.

    <div class="demobox">
      <span style="color: grey">{{ this.start }}</span><span style="color: aqua"><strong>RetroCursorText</strong></span><span style="color: aqua"> text</span>="Hello world..." <span style="color: aqua"> hideCursorEnd </span> <span style="color: grey">{{ this.end }}</span>
    </div>
    <div class="demobox">
      <RetroCursorText text="Hello world..." hideCursorEnd />
    </div>

    <h4>Fonts:</h4>
    This component uses the fonts <strong>Press Start 2P</strong> and
    <strong>VT323</strong> from Google Fonts. You can also manually import them
    in your stylesheet, if you so wish:
    <div class="demobox">
      <span style="color: fuchsia">@import</span> <span style="color: goldenrod"> url</span>('https://fonts.googleapis.com/css?family=Press+Start+2P|VT323&display=swap');
    </div>
  </div>
</template>

<script>
import RetroCursorText from './RetroCursorText.vue'
export default {
  name: 'Main',
  data() {
    return ({
      start: '<',
      end: '/>',
      text: [
        'Hello world...',
        'This is a demonstration of the',
        'Vue component VueRetroCursorText.',
        'It can be used to create retro',
        'console-style text animations',
        'in Vue projects.',
        'Thank you for checking it out!'
      ]
    })
  },
  components: {
    RetroCursorText
  }

}
</script>

